import React, { useEffect, useContext, useState } from 'react';
import styled from 'styled-components'
import { useLocation } from "react-router-dom";

import NavBar from '../components/NavBar'
import Ticket from '../components/Ticket'

import { SettingContext } from '../context/SettingContext'
import { Firebase, db } from '../config/firebaseConfig'


const ResultPage = () => {
  const [ result, setResult ] = useState(null)
  const [ setting, setSetting ] = useContext(SettingContext)
  const location = useLocation();
  const registerData = location.payload

  useEffect(() => {
    console.log('...')
    if (setting.isSubmitting) {
      sendingData()
    }
  }, [])

  const onClickClose = () => {
    window.open('','_self').close(); 
    //   
    window.location.href="kakaotalk://inappbrowser/close"
  }

  const sendingData = async () => {
    const submitData = {
      name: registerData.name.trim(),
      position: registerData.position.trim(),
      division: registerData.division.trim(),
      submitTime: registerData.submitTime,
      index: 0
    }

    const statsRef = db.collection(registerData.title).doc(registerData.serviceTime).collection(registerData.reservationDate).doc('--stats--');
    const newuserRef = db.collection(registerData.title).doc(registerData.serviceTime).collection(registerData.reservationDate).doc();
    const userListRef = db.collection(registerData.title).doc(registerData.serviceTime).collection(registerData.reservationDate)
    const increment = Firebase.firestore.FieldValue.increment(1)

    let query = userListRef.where("name", "==", registerData.name).where("position", "==", registerData.position);
    await query.get().then(querySnapshot => {
      if (querySnapshot.empty) {
        console.log(' ')
        db.runTransaction((transaction) => {
          return transaction.get(statsRef).then((statsDoc) => {
            if (!statsDoc.exists) {
              console.log('Document does not exist')
            } 
            const remainingCount = statsDoc.data().ReservationCount
            transaction.update(statsRef, { ReservationCount: increment }, { merge: true })
            transaction.set(newuserRef, {
              ...submitData,
              serverTime: Firebase.firestore.FieldValue.serverTimestamp(),
              index: remainingCount + 1,
            }) 
            }).then(() => {
              console.log("Transaction successfully committed!")
              setSetting({
                ...setting,
                isSubmitting: false
              })
            }).catch((error) => {
              console.log("Transaction failed: ", error)
            })
        })
      } else {
        console.log('   ')
        setSetting({
          ...setting,
          isSubmitting: false
        })
      }
    })
  }

  const checkResult = () => {
    const userListRef = db.collection(registerData.title).doc(registerData.serviceTime).collection(registerData.reservationDate)
    let indexQuery = userListRef.where("index", "==", 120)
    let query = userListRef.where("name", "==", registerData.name).where("position", "==", registerData.position);
    setTimeout(async () => {
      await query.get().then(querySnapshot => {
        if (querySnapshot.empty) {
          console.log('DB ')
          setResult('')
        } else {
          querySnapshot.forEach((doc) => {
            if (doc.data().index < 101) {
              console.log(' ')
              setResult('')
            } else if (doc.data().index >= 101 && doc.data().index < 121) {
              if (doc.data().index === 120) {
                console.log('120  ')
                indexQuery.get().then((querySnapshot) => {
                  let tempArray = []
                  querySnapshot.forEach((doc) => {
                    tempArray.push({
                     id: doc.id,
                     ...doc.data()
                    })
                 })
                 tempArray.sort((a, b) => {
                   return a.serverTime - b.serverTime
                 })
                 if (tempArray[0].name === registerData.name) {
                  console.log('120  ')
                  setResult('')
                 } else {
                  console.log('120  ')
                  setResult('')
                 }
                })
              } else {
                console.log(' ')
                setResult('')
              }
            } else {
              console.log('120  ')
              setResult('')
            }
          })
        }
      })
    }, [1000])
  }

  console.log(result)
  console.log(setting)

  return (
    <Container>
      <NavBar />
      <ResultContainer>
        {setting.isSubmitting ? (
          <>
            <div>......</div>
            <div> </div>
          </>
        ) : (
          <>
          {result == null ? (
            <CheckContainer>
              <CheckButton onClick={() => checkResult()}></CheckButton>
              <div style={{fontSize: '1.25rem', marginBottom: '0.25rem', textAlign:'center'}}>   </div>
              <div style={{fontSize: '1.25rem', marginBottom: '0.25rem', textAlign:'center'}}> </div>
            </CheckContainer>
          ) : (
            <>
            {result === '' ? (
              <Ticket place='' color='#4FDAFF' isWeekday={setting.isWeekday} info={registerData}/>
            ) : (
              <>
              {result === '' ? (
                <Ticket place='' color='#57FFAD' isWeekday={setting.isWeekday} info={registerData}/>
              ) : (
                <>
                {result === '' && <Ticket place='' color='#F54F50' isWeekday={setting.isWeekday} info={registerData}/>}
                </>
                
              )}
              </>
            )}
            <CloseButton onClick={onClickClose}> </CloseButton>
            <div 
              style={{width: '90%', display:'flex', flexWrap:'wrap', textAlign:'center', justifyContent: 'center', marginBottom: '0.5rem'}}
            >
                      *^^*
            </div>
            </>
          )}
          </>
        )}
      </ResultContainer>
    </Container>

  )
}

const Container = styled.div`
  position: relative;
  top: 0;
  height: 100vh;
`;

const ResultContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
  min-height: calc(100vh - 50px);
`;

const CheckContainer = styled.div`
  margin-bottom: 2rem;
`;

const CheckButton = styled.div`
  margin-bottom: 1rem; 
  background-color: #4D826C;
  padding: 0.75rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 1.5rem;
  font-weight: 600;
  border-radius: 16px;
`;

const CloseButton = styled.div`
  margin: 20px 0;
  border: 2px solid #228be6;
  color: #222;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export default ResultPage;